





















































































































































import Vue from "vue";
import { Prop, Component } from "vue-property-decorator";
import { AttributesDict } from "@/data/Furniture";
import { FClass } from "@/data/furniture/Physical";
import PhysicalAttr from "./EditCard/PhysicalAttr.vue";
import ConditionalDate from "./EditCard/ConditionalDate.vue";
import DatePickerMenu from "./EditCard/DatePickerMenu.vue";
import AttributeQuestion from "./EditCard/AttributeQuestion.vue";

@Component({
  components: {
    PhysicalAttr,
    ConditionalDate,
    DatePickerMenu,
    AttributeQuestion,
  },
})
export default class EditCard extends Vue {
  @Prop({ default: true })
  isEdit!: boolean;

  @Prop({ default: false })
  isStaff!: boolean;

  valid = true;

  required = [(v: any) => !!v || "This is required"];

  emailRules = [
    (v: any) => !!v || "This is required",
    (v: any) => /.+@.+/.test(v) || "E-mail must be valid",
  ];

  id = "";

  donorName = "";

  phone = "";

  email = "";

  address = "";

  zone = "";

  fclass = "";

  classOptions = Object.keys(FClass);
  // size = -1;
  // material = "";
  // materialAlt = "";
  // set = false;
  // hasFrame = false;
  // hasBoxSpring = false;
  // numChairs = -1;
  // heavy = false;

  dateOffered = new Date().toISOString().substr(0, 10);

  pickupBy = new Date().toISOString().substr(0, 10);

  urgent = false;

  confirmedPickupDate = "";

  dateCollected = "";

  dateDelivered = "";

  attributes = Object.keys(AttributesDict);

  partsIntact = false;

  finishIntact = false;

  smokeFree = false;

  petFree = false;

  bedbugFree = false;

  mildewFree = false;

  donateToFriend = false;

  comments = "";

  staffNotes = "";

  updateAttr(attr: string, value: boolean) {
    switch (attr) {
      case "partsIntact":
        this.partsIntact = value;
        break;
      case "finishIntact":
        this.finishIntact = value;
        break;
      case "smokeFree":
        this.smokeFree = value;
        break;
      case "petFree":
        this.petFree = value;
        break;
      case "bedbugFree":
        this.bedbugFree = value;
        break;
      case "mildewFree":
        this.mildewFree = value;
        break;
      case "donateToFriend":
        this.donateToFriend = value;
        break;
      default:
        break;
    }
  }
}
